@import "null.css";
@import "popup.css";

.d_none {
  display: none;
}

.visually_hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

#root {
  width: 100%;
  height: 100%;

  background-color: grey;
  font-size: 1.3rem;
  padding: 0 20px 60px;
}

.timeline {
  width: 60%;
  height: 100%;
  margin: 0 auto; 
  /* background-color: grey; */
  /* padding: 1em; */
   /* border: 1px solid #000;  */
  /* overflow: auto; */
  /* box-shadow: $b-sh-base; */
   &::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
  &__item {
    padding: 1em;
    border: 1px solid #000;
    border-radius: 10px;
    background-color: #fff; */
    /* box-shadow: $b_sh-light; */
     &:nth-child(n + 2) {
      margin-top: 1em;
    }
    .timeline-item__data {
      text-align: right;
    }
    .timeline-item__content {
      margin-top: 0.5em; 
      /* text-align: center; */
      overflow-wrap: break-word;
    }
    .timeline-item__geo {
      margin-top: 0.5em;
      .geo__icon {
        margin-left: 1em;
        align-items: center;
        .geo-icon__svg {
          cursor: pointer;
          width: 20px;
          height: 20px;
          margin-bottom: -3px;
        }
      }
    }
  }
} 

.form {
  min-height: 10%;
  width: 60%;
  position: fixed;
  bottom: 20px;
  background-color: #fff;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 0 1em;
  border: 1px solid #000;
  border-radius: 10px; 
  /* box-shadow: $b-sh-base; */
   &__actions {
    display: flex;
    align-items: center;
    position: relative;
  }
  &__preview-video {
    position: absolute;
    width: 100%; 
    /* height: 700px; */
     bottom: 45px;
  }
  &__record-btnOK,
  &__record-btnCancel,
  &__camera,
  &__micro { 
     display: inline-block;
    width: 30px;
    height: 30px;
    margin-left: 1em;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    margin-bottom: -7px;
    cursor: pointer;
  }
  &__record-btnOK,
  &__timer,
  &__record-btnCancel {
    position: absolute;
  }
  &__timer {
    display: inline-block;
    margin-left: 1em;
    padding: 11px 0;
    right: 45px;
    bottom: 0;
  }
  &__record-btnOK {

    border: 1px solid #000;
    border-radius: 50%;
    right: 120px;
    bottom: 14px;
  }
  &__record-btnCancel {

    border: 1px solid #000;
    border-radius: 50%;
    right: 0;
    bottom: 14px;
  }
   &__camera {

  }
  &__micro {

  }
 &__input {
    min-height: 10%;
    flex: 1 0;
    padding: 0.5em 0;
    font-size: inherit;
  }
}

.red::placeholder {
  color: red;
}

.form__micro {
  border: 1px solid #000;
  border-radius: 5px;
  background-color: greenyellow;
}
.form__micro:hover {
  cursor: pointer;
}

.form__camera {
  border: 1px solid #000;
  border-radius: 5px;
  background-color: greenyellow;
}
.form__camera:hover {
  cursor: pointer;
}

.form__record-btnOK {
  border: 1px solid #000;
  border-radius: 5px;
  background-color: green;
}
.form__record-btnOK:hover {
  cursor: pointer;
}

.form__record-btnCancel {
  border: 1px solid #000;
  border-radius: 5px;
  background-color: red;
}
.form__record-btnCancel:hover {
  cursor: pointer;
}

.timeline__item {
  margin: 2%; 
  background-color: rgb(233, 233, 233);
  padding: 1%;
  border: 1px solid #000;
  border-radius: 10px; 
}

.form__media {
  text-align: right;
}

video {
  width: 95%;
}

.form__input {
  min-height: 5%;
  width: 50%;
}