.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    overflow-y: auto;
    overflow-x: hidden;
    transition: all 0.8s ease 0s;
  }
  #description {
    height: 150px;
  }
  .popup.open {
    opacity: 1;
    visibility: visible;
  }
  .popup.open .popup__content {
    transform: perspective(600px) translate(0px, 0%) rotateX(0deg);
    opacity: 1;
  }
  .popup__body {
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 10px;
  }
  .popup__content {
    font-size: 1.3rem;
    background-color: #fff;
    color: #000;
    max-width: 700px;
    /* min-width: 500px; */
    padding: 30px;
    position: relative;
    transition: all 0.8s ease 0s;
    opacity: 0;
    transform: perspective(600px) translate(0px, -100%) rotateX(45deg);
  }
  .popup__title {
    text-align: center;
    font-size: 1.6rem;
    margin: 0 0 1em;
  }
  .popup__label {
    display: block;
    margin-top: 30px;
  }
  .popup__name {
    margin-bottom: 10px;
  }
  .popup__input {
    border: 1px solid #000;
    padding: 5px 15px;
    border-radius: 15px;
    width: 100%;
    font-size: 1.6rem;
  }
  .popup__buttons {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
  
  }
  .button {
    width: 200px;
    font-size: inherit;
    border: 1px solid #000;
    padding: 10px 15px;
    border-radius: 15px;
    color: #fff;
  }
  .button[data-name="save"] {
    background-color: rgb(48, 158, 48);
  }
  .button[data-name="change"] {
    background-color: rgb(48, 158, 48);
  }
  .button[data-name="cancel"] {
    background-color: rgb(119, 119, 119);
    margin-left: 60px;
  }
  .button[data-name="cancel"].green {
    background-color: rgb(48, 158, 48);
    margin: 0 auto;
  }
  .button[data-name="yes"] {
    background-color: rgb(255, 94, 94);
  }
  .button[data-name="no"] {
  background-color: rgb(119, 119, 119);
  margin-left: 60px;
  } 